import React, { Component} from 'react';
import { CardList } from './components/card-list/card-list.component'
import { SearchBox } from './components/search-box/search-box.component'
import './App.css';

class App extends Component {
    constructor() {
        super();
        this.state = {
           monsters: [],
           fieldSearch: ''
        }
    }
    componentDidMount() {
        fetch("https://jsonplaceholder.typicode.com/users")
        .then(res => res.json())
        .then(users => this.setState({ monsters: users }))
    }

    render() {
        const { monsters, fieldSearch } = this.state;
        const monstersFilter = monsters.filter(monster =>
            monster.name.toLowerCase().includes(fieldSearch.toLowerCase())
        );

        return (
            <div className="App">
                <h1>Monsters Rolodex</h1>
                <SearchBox placeholder="Search monsters"
                    handleChange={e => this.setState({ fieldSearch: e.target.value })}
                />
                <CardList monsters={monstersFilter} />
            </div>
        );
    }
}

export default App;
